import React, { Component } from 'react'
import reactHtmlParser from "react-html-parser"
import {withTranslation} from 'react-i18next';

export class WhenBox extends Component {
    render() {

        const {t} = this.props;

        return (
            <div className="gardient-red">
               <div className="container container-wrapper">
                  <div className="row">
                     <h2 className='text-white'>{this.props.heading}</h2>
                     <p className='text-white'>{reactHtmlParser(this.props.description)}</p>
                     <div className='col-lg-12'>
                     <ul className='text-white'>
                         {this.props.h2List && this.props.h2List.map(whenbox => <li>{reactHtmlParser(t(whenbox))}</li>)}
                     </ul>
                     </div>

                  </div>
               </div>
            </div>
        )
    }
}

export default withTranslation() (WhenBox)
