import React, { Component } from 'react';
import reactHtmlParser from "react-html-parser";
import {withTranslation} from 'react-i18next';

export class WhySection extends Component {
    render() {
        const{t} = this.props;
        return (
            <div className="whyInvest">
                  <h2>{this.props.h2Heading}</h2>
                  <p>{reactHtmlParser(this.props.description)}</p>
                  <ul>
                      {this.props.h2List && this.props.h2List.map(h2s =><li>{reactHtmlParser(t(h2s))}</li>)}

                     </ul>
               </div>
        )
    }
}

export default withTranslation() (WhySection)
