import { t } from 'i18next'
import React, { Component } from 'react'
import reactHtmlParser from "react-html-parser"

export class HowIToInvest extends Component {
    render() {
        return (
            <>
            <div className="col-lg-5 getStartedBtn">
                <h2>{t(this.props.data[0].heading)}</h2>
                <p>{t(this.props.data[0].innerdescription)}</p>
            <a href="https://app.nivesh.com/home/index" target="_blank" className='btn btn-primary' title="">{t('CTAFooter:investingNowbtn')}</a>
            </div>
         <div className="col-lg-7">
            {this.props.data && this.props.data.map((howinvest, index) =>{
              if(index===0) return(<></>);
              return(
               <div className="stepWrp">
                <div className="stepLft"><h3>{t(`${howinvest.stepid}`)}</h3></div>
                <div className="stepRgt"><p>{reactHtmlParser(t(`${howinvest.description}`))}</p></div>
                </div>
                );
            })}
         </div>
         </>
        )
    }
}

export default HowIToInvest
