import React, { Component } from 'react'
import reactHtmlParser from "react-html-parser"
import redCircle from '../Layouts/Home/images/Ellipse 48.png'

export class featuresbox extends Component {

    render() {

        return (
             <>

            <div className='clearDiv'></div>
            <div className="featureMidCapDiv">
                     <div className="iconMidCap">
                     <img src={redCircle} alt="" className='red-circle img-fluid' />
                        <img src={this.props.image} alt="" className='iconImage'/>

                     </div>
                     <div className="cntMidCap">
                        <h3>{this.props.title}</h3>
                     </div>
                     <div className="clearDiv"></div>
                     <p>{ reactHtmlParser(this.props.description)}</p>
                  </div>
                  </>
        )
    }
}

export default featuresbox
